import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import { BreadCrumbs } from '../components/breadcrumbs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FormattedMessage, Link, useIntl } from 'gatsby-plugin-intl';

const NewsPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile } = data;
  const taos = allFile.edges.find((v) => v.node.name === 'taos').node.childImageSharp.fluid;

  function renderContent(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <Container>
            <Row></Row>
          </Container>
        );
      case 'es':
        return (
          <Container>
            <Row></Row>
          </Container>
        );
      default:
        return (
          <Container>
            <Row>
              <Col>
                <Img fluid={taos} className="img-fluid" alt="TAOS" />
              </Col>
            </Row>
          </Container>
        );
    }
  }

  return (
    <Layout>
      <SEO
        title={intl.formatMessage({ id: 'news-seo-title' })}
        description={intl.formatMessage({ id: 'news-seo-description' })}
        keywords={intl.formatMessage({ id: 'news-seo-keyword' })}
      />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="whatsnew" />
                  <h1 className="page-title">
                    <FormattedMessage id="whats-new" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="whats-new-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          {renderContent(intl.locale)}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allFile(filter: { name: { in: ["taos"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default NewsPage;
